import React, { useState } from "react";
import { useStoreState, useStoreDispatch } from "./Store";
import InputForm from "./InputForm";
import ActiveLabel from "./ActiveLabel";

export default function SettingsDisplay() {
    const state = useStoreState();
    const dispatch = useStoreDispatch();
    const [active_input, set_active_input] = useState("workout_name");
    const [input_value, set_input_value] = useState("");
    const [display_form, set_display_form] = useState(false);

    function toggle_display() {
        console.log("toggle display clicked");
        set_display_form(!display_form);
    }

    function handle_change(e) {
        set_input_value(e.target.value);
    }

    function handle_save() {
        if( !active_input ) {
            return;
        }
        const val = (
            active_input === "workout_name"
                ? input_value
                : Number(input_value)
        );

        dispatch({
            type: "SET_CONFIG",
            payload: {
                id: active_input,
                value: val
            }
        });
        set_input_value("");
        toggle_display();
    }

    function handle_cancel() {
        toggle_display();
        set_input_value("");
        set_input_value("");
    }

    function handle_active_label_click(id) {
        set_active_input(id);
        toggle_display();
    }

    return (
        <>
            {
                display_form
                    ? (
                        <>
                            <InputForm
                                id={active_input}
                                label={active_input.replace("_", " ")}
                                handleCancel={handle_cancel}
                                handleChange={handle_change}
                                handleSave={handle_save}
                                placeholder={state.config[active_input]}
                                type={active_input === "workout_name" ? "text": "number"}
                                value={input_value}
                            />
                        </>
                    )
                    : (
                        <ul className="list-unstyled">
                            <li>
                                <ActiveLabel handleClick={() => handle_active_label_click("workout_name")}>
                                    <span className="mr-2">Workout Name:</span> {state.config.workout_name}
                                </ActiveLabel>
                            </li>
                            <li>
                                <ActiveLabel handleClick={() => handle_active_label_click("intervals")}>
                                    <span className="mr-2">Intervals:</span> {state.config.intervals}
                                </ActiveLabel>
                            </li>

                            <li>
                                <ActiveLabel handleClick={() => handle_active_label_click("sets")}>
                                    <span className="mr-2">Sets:</span> {state.config.sets}
                                </ActiveLabel>
                            </li>
                            <li>
                                <ActiveLabel handleClick={() => handle_active_label_click("rest")}>
                                    <span className="mr-2">Rest:</span> {state.config.rest}
                                </ActiveLabel>
                            </li>
                            <li>
                                <ActiveLabel handleClick={() => handle_active_label_click("work")}>
                                    <span className="mr-2">Work:</span> {state.config.work}
                                </ActiveLabel>
                            </li>
                        </ul>
                    )
            }
        </>
    );
}