import React from "react";
import { createUseStyles } from "react-jss";

const useStyles = createUseStyles({
    wrapper: {
        composes: "pt-3",
        cursor: "pointer",
        "&:hover": {
            color: "blue",
            textDecoration: "underline"
        }
    }
})

export default function ActiveLabel({
    children,
    handleClick
}) {
    const styles = useStyles();

    return (
        <>
            <div
                className={styles.wrapper}
                onClick={handleClick}
            >
                { children }
            </div>
            <hr className="mb-3" />
        </>
    );
}